@import url(https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.6/dist/web/static/pretendard.css);
body {
    font-family: -apple-system, BlinkMacSystemFont, "Apple SD Gothic Neo", "Pretendard Variable", Pretendard, Roboto, "Noto Sans KR", "Segoe UI", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
}
* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

#container {
text-align: center;
padding: 20px;
}
p {
font-size: 12px;
padding-bottom: 20px;
margin-bottom: 20px;
border-bottom: 1px solid #ccc;
}
#container h2 {
font-weight: 600;
line-height: 2;
}
ul {
list-style-type: none;
}
.menu {
display: -webkit-box;
display: -ms-flexbox;
display: flex;
margin-bottom: 20px;
}
.menu > li {
width: 33%;
display: -webkit-box;
display: -ms-flexbox;
display: flex;
-webkit-box-align: center;
    -ms-flex-align: center;
        align-items: center;
cursor: pointer;
}
.rank_wrap {
width: 50px;
background-color: aliceblue;
margin-right: 10px;
}
.rank_wrap > span {
display: block;
}
.rank {
font-size: 30px;
}
.small {
font-size: 12px;
background-color: rgb(34, 109, 173);
color: #ffff;
}
.title {
background-color: #f7f7f7;
width: calc(100% - 70px);
height: 100%;
display: -webkit-box;
display: -ms-flexbox;
display: flex;
-webkit-box-align: center;
    -ms-flex-align: center;
        align-items: center;
-webkit-box-pack: center;
    -ms-flex-pack: center;
        justify-content: center;
}
.menu > li.active .small {
background-color: black;
}
.menu > li.active .rank {
color: #fff;
background-color: black;
}
.menu > li.active .title {
background-color: rgb(34, 109, 173);
color: #fff;
}
.img img {
width: 400px;
}
.content {
display: -webkit-box;
display: -ms-flexbox;
display: flex;
padding: 0 20px 0 0;
}
.content_list {
text-align: left;
margin-left: 20px;
}
.content_list li {
border-bottom: 1px solid #eee;
line-height: 2;
}    
